$font-stack: -apple-system, GeometriaLight, sans-serif;

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: 'CodePro';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/CodePro/Code-Pro.ttf);
}

@font-face {
  font-family: 'GeometriaLight';
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/Geometria/Geometria-Light.ttf);
}

@font-face {
  font-family: 'GeometriaMedium';
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/Geometria/Geometria-Medium.ttf);
}

.none {
  padding: 0;
  margin: 0;
}

html {
  min-width: 1240px !important;
}

.container {
  width: 1240px !important;
}

body {
  font-family: $font-stack;
  min-width: 1240px !important;
}

header {
  .redHead {
    background-color: #ed553b;
    padding: 15px 34px 18px 34px;
    margin: 0;
    font-size: 20px;
    font-family: GeometriaMedium;
    line-height: 32px;
    display: inline-block;
    color: #fff;
    text-align: center;
    /*width: 188px;*/

    span {
      font-size: 30px;
    }
  }
  .redItems {
    /*width: 86%;*/
  }
  .redItem {
    width: 33%;
    font-size: 14px;
    display: inline-block;
    font-family: GeometriaMedium;
    line-height: 19px;
    padding: 21px 0 0 0;
    text-align:center;
    letter-spacing: 0.5px;
    vertical-align: top;
    height: 100%;
    a {
      display: inline-block;
      text-align: left;
      color: #07131a;
    }
    &:hover {
      background-color: #ed553b;
      a {
          text-decoration: none;
          color: #fff;
      }
    }
  }
}

