.screenFour {
  .headCourses {
    padding-top: 36px;
    margin-left: -116px;
    h3 {
      font-size: 38px;
          font-family: GeometriaMedium;
          color: #1f1f1f;
          margin: 0 0 0 0;
          letter-spacing: -1px;
          line-height: 44px;
          text-align: right;
      span {
        display: inline-block;
            text-align: right;
            float: right;
            margin-right: 0;
            background-color: #fad67b;
            padding: 0 3px 5px 0;
      }
    }
    ul {
      list-style-type: none;
      font-size: 23px;
      font-family: GeometriaMedium;
      color: #1f1f1f;
      margin: 0;
      padding: 8px 0 0 30px;
      letter-spacing: -0.5px;
      line-height: 24px;
      li {
        img {
          width: 15px;
          margin-right: 10px;
          }
        }
      }
    }
    .itemsCourses {
      margin-top: 114px;
      .greyContainer {
        background-color: #dddddd;
        width: 398px;
        margin-left: 163px;
        img {
          width: 283px;
          margin-top: -70px;
          margin-left: 57px;
        }
        .sliderCourses {
          position: relative;
          margin-top: 60px;
          padding-bottom: 19px;
          .slick-list {
            min-height: 75px !important;
          }
          .slick-slide {
            font-size: 15px;
            width: 398px;
            letter-spacing: -0.5px;
            text-align: center;
            padding: 0 51px;
            a {
              color: #1f1f1f;
            }
          }
          .but-next {
            border: none;
            outline: none;
            background-color: #ed553b;
            color: #fff;
            font-size: 24px;
            position: absolute;
            z-index: 1000;
            width: 41px;
            height: 41px;
            top: -7px;
            right: -14px;
          }
          .but-prev {
            border: none;
            outline: none;
            background-color: #ed553b;
            color: #fff;
            font-size: 24px;
            position: absolute;
            z-index: 1000;
            top: -6px;
            left: -14px;
            width: 41px;
            height: 41px;
          }
          .slick-dots {
            list-style-type: none;
            margin: 0;
            padding: 0;
            text-align: center;
            li {
              display: inline-block;
              border: 1px solid #ed553b;
              width: 9px;
              height: 9px;
              margin: 0 9px;
              cursor: pointer;
              button {
                display: none;
              }
            }
            li.slick-active {
              background-color: #ed553b;
            }
          }
        }
      }
      div:nth-child(2) > .greyContainer {
          margin-left: 30px;
          img {
            width: 186px;
            margin-left: 90px;
            margin-top: -70px;
          }
      }
    }
}

