    margin-top: 123px;
    padding: 39px 0 34px 0;
    position: relative;
  .anchorCenter {
    position: absolute;
    top: -18px;
    right: 224px;
    color: #fff;
    letter-spacing: -0.1px;
    background-color: #ed553b;
    padding: 7px 9px;
    font-size: 15px;
  }
  .socialAnchors {
    padding: 25px 0 25px 0;
    text-align: center;
    a {
      font-size: 20px;
      color: #fff;
      padding-right: 63px;
    }
  }
}
